.header__logo {
  position: relative;
}
.header__logo::after {
  content: "";
  display: block;
  width: calc(100% + 30px);
  height: 1px;
  background-color: #d9d8d4;
  position: absolute;
  bottom: -12px;
  left: -15px;
}
