  }
}
.header__logo-img {
  height: 28px;
  object-fit: cover;
}
@media screen and (min-width: 1200px) {
  .header__logo-img {
    height: 36px;
    width: 100%;
    object-fit: cover;
  }
}
.header__navigation {
